<template>
  <div class="activity-box" id="activity">
    <div>
      <div class="activity-top">
        <h1>{{ $t("activity.title") }}</h1>
        <p>{{ $t("activity.describe") }}</p>
      </div>
      <div class="activity-view">
        <div class="activity-item" v-for="(item, index) in activityList" :key="index">
          <img :src="'./static/image/' + item.img" alt="" />
          <div>
            <p>{{ item.title }}</p>
            <p>{{ item.describe }}</p>
            <p>
              <span>{{ item.time }}</span><span class="more" @click="onDetails(item, index)">{{ $t("activity.buttonText")
              }}
                &gt;</span>
            </p>
          </div>
        </div>
      </div>
      <div class="activity-more">
        <el-button round @click="onActivityList">
          {{ $t("activity.more") }}</el-button>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data: () => {
    return {
      activityList: [],
      keepScroll:0
      // list: require('@/static/data.js'),
    };
  },
  computed: {
    list() {
      return []
    },
  },
  created() {
    this.intData()
  },
  methods: {
    intData() {
      //引用axios
      const lang = localStorage.getItem("lang");
      switch (lang) {
        case 'en':
          axios.get("/static/lang/en.json", {})
            .then(({ data }) => {

              this.activityList = data.ActivityList.reverse();
              if (this.activityList.length >= 6) {
                this.activityList.length = 6;

              }
            })
          break;
        case 'Czh':
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/Czh.json", {})
            .then(({ data }) => {
              this.activityList = data.ActivityList.reverse();
              if (this.activityList.length >= 6) {
                this.activityList.length = 6;

              }
            })

          break;

        default:
          //获取数据（访问路径不用带public ，直接访问public下的文件即可）
          axios.get("/static/lang/zh.json", {})
            .then(({ data }) => {

              this.activityList = data.ActivityList.reverse();
              if (this.activityList.length >= 6) {
                this.activityList.length = 6;

              }
            })

          break;
      }

    },
    onActivityList() {
      this.$router.push("/activity");
    },
    onDetails(item, index) {
       const scrollTop = Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop)
        this.keepScroll = scrollTop;
      localStorage.setItem("details", JSON.stringify(item))
      this.$router.push({ path: "/activity/details", query: { index } });
    },
  },
};
</script>
<style lang="less" scoped>
