      }

      P:nth-of-type(1) {
        display: -webkit-box;
        /* Safari */
        -webkit-line-clamp: 2;
        /* Safari and Chrome */
        -webkit-box-orient: vertical;
        overflow: hidden;
        /* Safari and Chrome */

        overflow: hidden;
